.titleBox {
    margin-top: 100px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-bottom: 2px solid #004435;
}
.titleBox p {
    margin-top: 20px;
}
.title {
    font-size: 18pt;
    font-weight: 800;
    margin-bottom: 0;
}
.date {
    width: 80%;
    text-align: end;
    font-size: 15pt;
    font-weight: 600;
    color: #555;
    margin-bottom: 20px;
}

.contentsBox {
    width: 60%;
    margin: auto;
    padding: 20px 0;
}
.imgBox {
   width: 100%; 
   text-align: center;
}

.img {
    width: 100%;
}

.linkBox {
    width: 60%;
    margin: auto;
}

.contentsBox div {
    text-align: center;
}
.contents {
    text-align: start !important;
}
.contents figure {
    display: flex;
    align-items: center;
    justify-content: center;
}

.btnBox {
    width: 100%;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.btnBox button {
    margin: 0 20px;
    font-size: 12pt;
    padding: 5px 15px;
    font-weight: 600;
    cursor: pointer;
}

.modify {
    color: #fff;
    border: 3px solid #004435;
    background-color: #004435;
    transition: all 0.3s ease;
}
.modify:hover {
    color: #004435;
    background-color: #fff;
}

.delete {
    color: #ff5555;
    background-color: #fff;
    border: 3px solid #ff5555;
    transition: all 0.3s ease;
}
.delete:hover {
    color: #fff;
    background-color: #ff5555;
}